import Confirm from './index.vue'

export default {
  /**
   * install
   * @param {Object} Vue - Vue
   * @param {Object} [pluginOptions] - 
   */
  install (Vue, pluginOptions = {}) {
    // ""
    const VueConfirm = Vue.extend(Confirm)
    let $vm = null

    /**
     * message
     * @returns {Promise} Promise
     */
    function $confirm (options) {
      return new Promise((resolve, reject) => {
        function mount () {
          $vm = new VueConfirm()
          // 
          $vm.$mount()
          // 
          document.querySelector(pluginOptions.container || 'body').appendChild($vm.$el)
        }
        // 
        if (!$vm) {
          mount()
        }
        if (typeof options === 'object') {
          for (const key in options) {
            $vm[key] = options[key]
          }
        } else {
          mount()
          $vm.message = options
        }
        $vm.$off('cancel')
        $vm.$off('confirm')
        // message
        $vm.show()
        $vm.$on('cancel', res => {
          $vm.hide()
          reject(options)
        })
        $vm.$on('confirm', res => {
          $vm.hide()
          resolve(options)
        })
      })
    }
    Vue.message = Vue.prototype.$confirm = $confirm
  }
}
